/*-------------
-  Base css
---------------*/
html, body {
  margin: 0;
  height: 100%;
  font-size: 100%;
  font-family: sans-serif;
  background: #fff; }

* {
  box-sizing: border-box; }

/**
* Components
*/
/*-------------
-  Buttons css
---------------*/
.button {
  position: relative;
  width: 100%;
  max-width: 300px;
  padding: 10px 30px;
  margin: 0.5em;
  color: #ff8364;
  border: 1px solid #ff8364;
  border-radius: 2%;
  transition: 0.5s all;
  cursor: pointer;
  text-align: center; }
  .button--grow {
    flex-grow: 1; }

/*-------------
-  Reader css
---------------*/
.reader {
  width: 100%;
  max-width: 600px;
  margin: 20px auto; }
